.skill{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 280px;
    background-color: lightpink;
    text-align: center;
    margin: 0 10px 10px;
} 
.icon{
    height: 50px;
    width: 50px;
    background-color: yellowgreen;
    transform: rotate(45deg);
    margin: 15px;
}
.skillTitle{
    color: #222222;
    margin: 0 0 25px;
    font-size: 15px;
    letter-spacing: 1px;
    font-weight: 600;
    font-family: 'Josefin Sans', sans-serif;
    text-transform: uppercase;
}
.description{
    color: rgba(34, 34, 34, 0.5);
    font-family: 'Opensans', sans-serif;
    line-height: 26px;
    font-size: 14px;
    margin: 0 0 10px;
}
.skillInfo{
    padding: 20px;
}
@media (max-width: 390px) {
    .skill{ width: 250px;}
 }