.footer{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgb(88, 64, 64);
    color: beige;
    outline: 1px solid;
}

.socialIcons{
    display: flex;
    margin: 15px;

}
.socialIcons li{
    display: block;
    width: 50px;
    height: 50px;
    background-color: blueviolet;
    transform: rotate(45deg);
    margin: 15px;

}
.title{
    background-color: rgb(134, 109, 75);
}
